@font-face {
  font-family: 'Bold';
  src: url('Roboto-Bold.ttf');
}
body {
  /*background: -webkit-linear-gradient(-60deg, #CCC 0%,#CCC 50%,#BBB 50%,#BBB 100%);*/
  background: radial-gradient(circle, #fff, #888);
  padding: 0;
  margin: 0;
  font-family: Roboto;
}

.container {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
}
:focus {
  outline: none;
  box-shadow: 0px 0px 0px transparent;
}
.calculator {
  background: #f0f0f0;
  box-shadow: 2px 3px 6px #c2c2c2;
}
.row {
  display: flex;
}
.displayBox {
  background: #1abc9c;
  padding: 0;
  height: 130px;
  overflow: hidden;
  padding-right: 10px;
  padding-bottom: -5px;
  display: block;
}
.displayBox p {
  width: 100%;
  background: transparent;
  border: none;
  color: #fff;
  float: right;
  text-align: right;
  display: inline-block;
  overflow: hidden;
}
.displayEarlier {
  height: 45px;
  font-size: 30px;
  font-weight: 50;
}
.displayOperator {
  height: 25px;
  font-size: 20px;
  font-weight: 100;
}
.displayResult {
  height: 45px;
  font-size: 30px;
  font-weight: 100;
}
.operationSide {
  background: #fff;
  margin-top: -14px;
  padding: 0;
  padding-left: -20px;
  padding-top: 10px;
  border-left: 1px solid #e5e5e5;
}
.numberPad {
  padding-top: 14px;
}
.btn-calc,
.btn-calc:focus {
  flex: 1;
  background: transparent;
  color: #a2a2a2;
  font-size: 25px;
  width: 60px;
  height: 60px;
  font-weight: 100;
  border-radius: 100px;
  outline: none;
  box-shadow: 0px 0px 0px transparent;
  border: none;
}
.btn-calc:hover {
  background: #fff;
  color: #1abc9c;
}
.btn-operation {
  background: transparent;
  color: #999;
  font-size: 25px;
  /*width: 97px;*/
  width: 100%;
  height: 80.8px;
  font-weight: 100;
  border-radius: 0;
  outline: none;
  box-shadow: 0px 0px 0px transparent;
}
.btn-operation:focus {
  background: #e8e8e8;
  color: #444;
  outline: 0;
}
.btn-operation:hover {
  background: #e8e8e8;
  color: #444;
}
.equals,
.equals:focus {
  background: #1abc9c;
  color: #fff;
}
.equals:hover {
  background: #76ecd4;
  color: #fff;
}
.clear,
.clear:focus {
  flex: 1;
  background: #1abc9c !important;
  color: #fff;
  font-size: 25px;
  width: 60px;
  height: 60px;
  font-weight: 100;
  margin: 5px;
  border-radius: 50%;
  outline: none;
  box-shadow: 0px 0px 0px transparent;
  border: transparent;
}
.clear:hover {
  background: #76ecd4;
  color: #fff;
}
.btn-question,
.btn-question:focus {
  z-index: 100;
  position: absolute;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 40px;
  border-radius: 40px;
  background: transparent;
  border: 2px solid #fff;
  padding-top: 6px;
  color: #76ecd4;
  font-family: sans-serif;
  font-size: 18px;
  outline: none;
}
.btn-question:hover {
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
}
.modal-dialog {
  padding-top: 100px;
  width: 450px;
}
.modal-content {
  border-radius: 0px;
  box-shadow: none;
  border: none;
}
.modal-title {
  color: #666;
  font-size: 15px;
}
.modal-text {
  line-height: 28px;
  font-size: 15px;
  color: #555;
  padding: 10px 5px;
  padding-right: 10px;
}
.close {
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  transition: all 0.5s;
}
.modal-close {
  font-weight: 100;
}
.btn-social,
.btn-social:focus {
  background: #f0f0f0;
  padding: 10px 12px;
  border-radius: 2px;
  color: #888;
  margin-right: 5px;
  text-transform: uppercase;
  font-size: 11px;
  font-family: Bold;
}
.linkedin:hover {
  background: #1abc9c;
  color: #fff;
}
.twitter:hover {
  background: #76ecd4;
  color: #fff;
}
.github:hover {
  background: #444;
  color: #fff;
}
.link,
.link:focus {
  border-bottom: 1px dotted #1abc9c;
  color: #1abc9c;
}
.link:hover {
  color: #76ecd4;
  border-color: #76ecd4;
}

/* MEDIA QUERIES */
@media only screen and (max-width: 768px) {
  body {
    padding-top: 1.3em;
  }
  .calculator {
    height: 590px;
    box-shadow: 2px 3px 6px #888;
  }
  .displayBox {
    height: 150px;
  }
  .btn-calc,
  .btn-calc:focus {
    width: 70px;
    height: 70px;
    font-size: 18px;
  }
  .operationSide {
    padding: 0;
  }
  .btn-operation,
  .btn-operation:focus {
    width: 60px;
    height: 60px;
    font-size: 18px;
  }
  .clear,
  .clear:focus {
    flex: 1;
    width: 70px;
    height: 70px;
    font-size: 18px;
  }
  .equals,
  .equals:focus {
    width: 62px;
    height: 63px;
    margin-top: 1px;
  }
  .modal-dialog {
    width: 100%;
  }
}
@media only screen and (min-width: 1440px) {
  .container {
    width: 85%;
  }
  .btn-calc,
  .clear,
  .btn-calc:focus,
  .clear:focus {
    width: 80px;
    height: 80px;
  }
  .equals,
  .equals:focus {
    width: 100%;
    height: 96px;
  }
}

.hvr-radial-out:before {
  background: #fff !important;
  border-radius: 100%;
  transition-duration: 0.5s;
}
.hvr-fade:before {
  background: #fff !important;
  transition-duration: 0.5s;
}
